var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('section',{staticClass:"section section-icons pink  center"},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col s12 m8"},[_c('div',{staticClass:"card-panel z-depth-5"},[_c('h5',{staticClass:"left-align"},[_vm._v("Professor Criner's Higher Education Preparation Plan")]),_c('p',{staticClass:"left-align"},[_vm._v("\n             The education of our young people is a long term process and \n             should be planned by individual families and not abdicated to the \n             education system.  The school systems cannot do everything that \n             needs to be done and parental involvement is necessary for the \n             successful of an educational plan for young people.  These are \n             some general guidelines on college preparation for parents and \n             prospective college students. \n          ")]),_c('img',{staticClass:"imglarge responsive",attrs:{"src":require("../../assets/images/bls earnings data.gif")}}),_c('h6',{staticClass:"center-align"},[_vm._v("Pre-School and Elementary School Preparation")]),_c('p',{staticClass:"left-align"},[_vm._v("\n            Children begin to learn  in the womb and learning is continuous \n            throughout life. New knowledge about pre-natal development suggests \n            that the behavior of the mother before birth has a great deal to do \n            with the health and well-being of the child after birth.  However, \n            very old knowledge also appears to be very applicable. Education \n            begins in infancy and continues through life. Elementary school \n            children need memory enhancement and pattern recognition training. \n            This may be done by memorizing poems, bible verses, studying a \n            musical instrument, studying art along with the school curriculum.\n          ")]),_c('h6',{staticClass:"center-align"},[_vm._v("\n            Middle School and Junior High School Preparation\n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n            Middle and junior high school is a  critical time for the creation \n            of scholastic abilities.  Students at this time should be exposed \n            to a wide variety of subjects and activities. Care should be taken \n            to ensure the continued study of areas that enhance memory and \n            pattern recognition. Study activities should include English \n            grammar, writing, history, geography, general science, music, \n            art, shop activities, and lots of problem solving with arithmetic \n            and geometry. It should not include algebra until the ninth grade \n            or later.  There is enough material on problem solving using \n            arithmetic and geometry to provide interesting challenges to \n            students without introducing them to algebra. There are large \n            numbers of students in college who have missed these arithmetic \n            fundamentals and cannot combine two fractions.\n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n            Students who miss the fundamentals of arithmetic and problem \n            solving at this level are handicapped for life!\n          ")]),_c('h6',{staticClass:"center-align"},[_vm._v("\n            High School Preparation\n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n            High school success is largely dependent upon the ninth grade hump. \n            If students can get through the ninth grade with their enthusiasm \n            about learning intact, then the tenth grade will be fairly smooth. \n            The tenth grade is another transition period for urban high school \n            students. English grammar, algebra, geometry, and biology are the \n            most important subjects of the 10th grade. \n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n            Geometry is the sleeper subject at this time and its subtleties are \n            misunderstood by teachers almost universally. At this point in the \n            educational process, geometry is the subject that first introduces \n            formal inference schemes. Many teachers miss this point and so some \n            schools have introduced an informal geometry course, which is a \n            complete waste of time. Students should have a full year of formal \n            plane geometry. Algebra is the next most important subject in high \n            school mathematics as college preparation.\n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n            Calculus in high school is premature and I discourage it.  I find \n            that most of the students in my classes have trouble with arithmetic \n            and algebra. If students don't know the algebra, then their study of \n            calculus does not matter.  Some schools offer pre-calculus which is \n            better. However, high schools should focus on algebra, geometry,  \n            trigonometry and solid geometry and leave the calculus for the \n            colleges.\n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n            Second year high school algebra is truly a must, as is English \n            composition, one year of biology,  chemistry, and physics.  \n            Trigonometry and solid geometry should also be included in the \n            senior year.  Again, calculus in high school should be avoided.\n          ")])])])])])])])}]

