 available.    \n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n            The brain has another capability also. It is what thinks. I do not \n            know what this thinking process is, but I do not think; my brain \n            thinks and passes the results of that process to my consciousness, \n            the user interface. \n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n            If I want to excel at scholarship, I must optimize the utilization \n            of my brain and provide for it the best environment to do the work \n            I want it to do for me. This means that as an associative memory, \n            my brain needs to have information entered in many different forms, \n            using different methods, and repeatedly often over time.  \n          ")])])])])])])])}]

