 \n           the subject. Each subject has an inferential process that is used \n           to make conclusions in the subject. This inferential process must \n           also be memorized and practiced. If you can explain a subject using \n           the inferential processes of the subject, then you understand the \n           subject.  \n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n           Repetition through practice is the method by which information is \n           implanted in animal memory. All animals learn in this manner. The \n           complexity of the processes being learned is what distinguishes \n           various animals. The ability to learn complex processes  is \n           dependent upon the species of the animal.  \n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n           The enormous variation in the methods to achieve  memory is what \n           brings about the conclusion that people learn in different ways. \n           However, all of the ways result in information being imbedded in \n           memory.  \n          ")])])]),_c('div',{staticClass:"col s12 m4"},[_c('div',{staticClass:"card-panel z-depth-5"},[_c('img',{staticClass:"responsive imglarge",attrs:{"src":require("../../assets/images/AndrewsPaigeCriner.gif")}}),_c('p',{staticClass:"left-align"},[_vm._v("\n            Old friends, Mrs. Naomi Cox Andrews, Former Secretary of \n            Education Rod Paige, and Professor Oscar Criner\n          ")]),_c('h5',{staticClass:"left-align"},[_vm._v("Neuropsychology")]),_c('p',{staticClass:"left-align"},[_vm._v("\n             Complex Systems and the Biological Basis of Knowledge\n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n              Priming the Pump: Creating a new generation of scientists\n          ")])])])])])])])}]

