aticClass:"left-align"},[_vm._v(" Some people claim to be good in some subjects but not in others.")]),_c('p',{staticClass:"left-align"},[_vm._v("\n            Some students believe that they can be good in English but not in \n            mathematics or vice versa. This is a fallacious notion. They are \n            not good in either and that is because they do not understand the \n            context. If they are able to understand the language, then they \n            should be able to understand any subject expressed in the language. \n            That they do not understand the mathematics means that they really \n            cannot read. It is the ability to make inferences in the language \n            that has been missed by many people. It probably was not taught in \n            the first place, because many teachers fail to understand how \n            inferences are made.\n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n             Why shouldn't all subjects be attainable be everyone? The fact is \n             that they should. Everyone should be able to understand any subject.  \n             There is no difference between the inferential scheme of an \n             automobile mechanic and that of a nuclear physicist. What is \n             different in these subjects is the vocabulary and process.\n          ")]),_c('br'),_c('h5',{staticClass:"left-align"},[_vm._v("\n             I will return to the following questions later.\n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n             How do you study? When do you study? How long do you study?\n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n             How do you know when you have studied enough?\n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n             Why are college classes spaced and scheduled at such odd times?\n          ")])])])])])])])}]

