var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('section',{staticClass:"section section-icons pink  center"},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col s12 m8"},[_c('div',{staticClass:"card-panel z-depth-5"},[_c('h4',{staticClass:"left-align"},[_vm._v("The Teaching and Learning Process")]),_c('img',{staticClass:"imglarge responsive",attrs:{"src":require("../../assets/images/Teaching and Learning.gif")}}),_c('p',{staticClass:"left-align"},[_vm._v("\n             Nothing is more confusing to students entering higher education \n             than the answers they get to the  important  questions, if they \n             even ask the  questions. Some students do not know that these \n             questions should be asked. \n          ")]),_vm._m(0),_c('p',{staticClass:"left-align"},[_vm._v("\n            These are not rhetorical questions or intended for extended \n            philosophical discourse. I intend to answer them with simple direct \n            answers and instructions that students can use to master subjects, \n            concepts,  and methods in your study in college.\n          ")]),_c('h5',{staticClass:"left-align"},[_vm._v("What is Learning?")]),_c('p',{staticClass:"left-align"},[_vm._v("\n            Learning is a process of life. Learning occurs when something is \n            done repeatedly over periods of time and in many different ways. \n            Learning is inevitable; if one lives, then one learns, in spite of \n            oneself. Habits are behavior learned inadvertently and are hard to \n            break for that reason.  Physical skills are learned and mental \n            skills are learned the same way, through persistence and practice. \n            Recall learning to ride a bicycle, or how to skateboard, or how to \n            swim. You can never forget how to perform those activities, even \n            though you may fail to be able to perform them  because of the \n            body's physical condition. Similarly learning to speak, sing, or \n            play music are activities that are unforgettable also.  The amazing \n            fact is that all experience is unforgettable, it is simply that we \n            do not know how to locate, i.e., remember, it.\n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n            The variety of ways of engaging a subject to be learned enhances to \n            ability for the brain to locate it.  The brain has an "),_c('router-link',{attrs:{"to":"associative_memory"}},[_vm._v("associative memory ")]),_vm._v(" and must \n            make connections with large numbers of events in \n            order to locate the desired process  or facts in memory. This is \n            why human beings mature much more slowly that do other animals. \n            Some animals learn to walk and run very soon after birth. In some \n            organisms, almost everything they need to know for survival is \n            present in the organism at birth. All of the information needed is \n            passed genetically and they know \"instinctively\" what to do. They \n            need very little nurturing by a parent organism. Parenting and \n            mentoring becomes necessary with animals that developed a cerebral \n            cortex and require external stimulus for learning.  Human beings, \n            some other primates, and other higher animals mature over a much \n            longer time during which they learn survival skills and the \n            responsibilities of life in the group.\n          ")],1),_c('h5',{staticClass:"left-align"},[_vm._v("\n            Study methods based upon these assumptions\n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n            A most effective method for the study of written material is to \n            perform active reading and writing along with other activities. \n            Read the material aloud. That allows you to hear the material. \n            Make detailed written notes, if not copy the material verbatim \n            engages the tactile senses. The study of mathematics and science \n            requires much more detailed written activity than nonscientific \n            subjects. Both reading and writing enhances the memory of the \n            material.\n          ")]),_c('h5',{staticClass:"left-align"},[_vm._v("\n            There is only one way to learn and that is through memory.\n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n             Students must know that many teachers are ambivalent about this \n             view of learning, because they believe that there is more to \n             learning that \"rote\" memory. They have a romantic notion that \n             human beings are special and different from other animals. Each \n             species of higher animal is different, but each has some kind of \n             brain that helps the organism to perceive the external world and \n             react to it. Within each species, the brain functions the same way, \n             unique to that species. This is clear from the well known fact that \n             dogs have a much more highly developed sense of smell than do human \n             beings.   \n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n            I am not ambivalent about the learning process and I hope that you \n            will see why. As a  teacher, I see clearly that my job is to \n            direct you, the student, into the desired activities. Teachers \n            devise activities and exercises for students to perform so that \n            they engage in the activity to be learned repeatedly.  Examine \n            the exercises in any mathematics textbook or music method and \n            you will see this process being implemented.  Teachers cannot \n            force students to perform the learning activities, they can \n            only encourage students and point out the direction to students. \n            It is entirely up to the student to do the work.   \n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n            People are usually confused about the functions and operations of \n            the human memory system. It appears to work outside of our \n            consciousness on issues that we present inside our consciousness. \n            The intensity of work toward learning a given process or solving a \n            problem often leads to the activity becoming \"second nature\", as it \n            were. The result is a demonstration of obvious excellence.  One can \n            only imagine the intensity of practice that Michael Jordan, Tiger \n            Woods, or Maurizio Pollini have experienced. The learning process is \n            the same in art, athletics or academics.\n          ")]),_c('h5',{staticClass:"left-align"},[_vm._v("\n            The relationship between knowing and understanding\n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n             Knowing is the internalization of the what we have studied and \n             practiced. To be able to regurgitate, as it were, the facts, or \n             repeat  the process, or perform the act demonstrates personal \n             knowledge. The result of the learning process is personal knowledge.  \n             Understanding, on the other hand, is quite a different matter. \n             This is at the crux of the confusion that belies our education \n             system.  The simplest way to state it is "),_c('router-link',{attrs:{"to":"teaching_learning2"}},[_vm._v("Understanding is Knowing \n             in Context\n             ")]),_vm._v(". \n          ")],1)])])])])])])}
