   ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n            Complex ideas are always communicated  in writing. In the knowledge \n            based economy, writing is an economic process.  All progress in  \n            science and technology depends upon persons who can write clearly \n            and lucidly.  Scientific explanations of phenomena , i.e.,  \n            theories, are expressed in the language of ordinary discourse.  \n          ")]),_c('p',{staticClass:"left-align"},[_vm._v("\n           Another well kept secret of the academic world is that problem \n           solving requires more writing  than mathematics. Software \n           development is creative writing. This is one of the most \n           misunderstood areas of the current technology. Problems solved \n           using science and engineering are first described in writing before \n           they are solved in any scientific or  engineering procedure or \n           computer program.    \n          ")])])]),_c('div',{staticClass:"col s12 m4"},[_c('div',{staticClass:"card-panel z-depth-5"},[_c('p',{staticClass:"left-align"},[_c('ol',[_c('li',[_vm._v("\n                  Geoffery Hughes,"),_c('i',[_vm._v(" Words in Time,")]),_vm._v(" Oxford, UK, Basil Blackwell, \n                  Ltd., 1988\n                ")]),_c('li',[_vm._v("\n                  Wilfred Funk,"),_c('i',[_vm._v(" Word Origins,")]),_vm._v(" Wings Books, 1950,(1992 Edition)\n                ")]),_c('li',[_vm._v("\n                  Gary Kirby and Jeffrey R. Goodpaster,"),_c('i',[_vm._v(" Thinking ")]),_vm._v(", Englewood \n                  Cliffs, NJ: Printice Hall, Inc., 1995\n                ")])])])])])])])])])}]

